import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import logo from '../assets/images/youtube-logo.png'
import '../styles/Sidebar.scss'
import {ReactComponent as Home} from '../assets/vectors/home.svg'
import {ReactComponent as Trend} from '../assets/vectors/trend.svg'
import {ReactComponent as Subscript} from '../assets/vectors/subscript.svg'
import {ReactComponent as History} from '../assets/vectors/history.svg'
import {ReactComponent as Library} from '../assets/vectors/library.svg'
import {ReactComponent as Later} from '../assets/vectors/later.svg'
import {ReactComponent as Liked} from '../assets/vectors/liked.svg'
import {ReactComponent as Music} from '../assets/vectors/music.svg'
import {ReactComponent as Star} from '../assets/vectors/star.svg'
import {ReactComponent as Game} from '../assets/vectors/game.svg'
import {ReactComponent as More} from '../assets/vectors/more.svg'
import User1 from '../assets/images/Oval.png'
import User2 from '../assets/images/users/Oval (1).png'
import User3 from '../assets/images/users/Oval (2).png'
import User4 from '../assets/images/users/Oval (3).png'
import User5 from '../assets/images/users/Oval (4).png'
import User6 from '../assets/images/users/Oval (5).png'

function burgerHide(e) {
    setTimeout(() => document.querySelector('.sidebar__logo').classList.toggle('sidebar__logo-hidden'), 100);
    setTimeout(()=>document.querySelector('.sidebar__links').classList.toggle('sidebar__links-hidden'),300)
    setTimeout(()=>document.querySelector('.sidebar__subs').classList.toggle('sidebar__subs-hidden'),500)
    document.querySelector('h4').style.display = document.querySelector('h4').style.display !== 'none'? 'none' : 'block' 
}
export default function Sidebar() {
    return (
        <div>
            <div className="sidebar">
                <div className="sidebar__top">
                    <div className="sidebar__burger" onClick={burgerHide}>
                        <span className="sidebar__burger-item"></span>
                        <span className="sidebar__burger-item"></span>
                        <span className="sidebar__burger-item"></span>
                    </div>
                    <div className="sidebar__logo">
                        <img src={logo} alt="" className='sidebar__logo-img' />
                        <div className="sidebar__logo-name">YouTube</div>
                    </div>
                </div>
                <ul className="sidebar__links">
                    <li className="sidebar__links-item"><NavLink to='/' exact className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Home/>Home</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/trend' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Trend/>Trending</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/subs' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Subscript/>Subscriptions</NavLink></li>
                    <br />
                    <li className="sidebar__links-item"><NavLink to='/lib' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Library/>Library</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/his' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><History/>History</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/later' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Later/>Watch later</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/fav' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Star/>Favourites</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/like' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Liked/>Liked</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/music' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Music/>Music</NavLink></li>
                    <li className="sidebar__links-item"><NavLink to='/game' className='sidebar__links-item-link' activeClassName='sidebar__links-item-link-active'><Game/>Games</NavLink></li>
                    <li className="sidebar__links-item"><span className='sidebar__links-item-link'><More/>Show more</span></li>
                </ul>
                <h4 style={{paddingTop:"10px",display:'block'}}>Subscriptions</h4>
                <ul className="sidebar__subs">
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User1} alt="" className="sidebar__subs-item-img" />Gussie Singleton</Link></li>
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User2} alt="" className="sidebar__subs-item-img" />Nora Francis</Link></li>
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User3} alt="" className="sidebar__subs-item-img" />Belle Briggs</Link></li>
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User4} alt="" className="sidebar__subs-item-img" />Eunice Cortez</Link></li>
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User5} alt="" className="sidebar__subs-item-img" />Emma Hanson</Link></li>
                    <li className="sidebar__subs-item"><Link to='/channel' className="sidebar__subs-item-link"><img src={User6} alt="" className="sidebar__subs-item-img" />Leah Berry</Link></li>
                </ul>
            </div>
        </div>
    )
}
