import React from 'react'
import Videos from '../components/Videos'
import { Route, Switch,Link } from 'react-router-dom'

//Images
import Bell from '../assets/images/Bell.png'
import Camera from '../assets/images/Camera.png'
import Dots from '../assets/images/Dots.png'
import UserPIc from '../assets/images/Userpic.png'

import '../styles/Navbar.scss'
import ChannelPage from './ChannelPage'
import VideosPage from './VideosPage'


export default function Navbar() {
    return (
        <div style={{width:'100%'}}>
            <div className='navbar'>
                <div className="navbar__search">
                    <input type="search" name="search-input" className='navbar__search-input' placeholder='Search'/>
                </div>
                <div className="navbar__others">
                    <img className="navbar__others-item" src={Camera} alt='' />
                    <img className="navbar__others-item" src={Dots} alt='' />
                    <img className="navbar__others-item" src={Bell} alt='' />
                    <Link to='/channel'><img className="navbar__others-accaunt" src={UserPIc} alt='' /></Link>
                </div>
            </div>
            <Switch>
                <Route path='/video' component={VideosPage} />
                <Route path='/channel' component={ChannelPage} />
                <Route path='/' component={Videos} />
            </Switch>
        </div>
    )
}
