import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/VideoCards.scss';

export default function VideoCards({
    cardImage = '../assets/images/Cover.png',
    videoName = 'A Brief History Of Creation',
    author = 'Dollie Blair',
    classOfImg = 'videocard-img',
    views = false,
    nameHidden = false,
    className = 'videocard'
}) {
    const time = `${Math.floor(Math.random() * 60)}:${Math.floor(Math.random() * 60)}`
    return (
        <div className={className}>
            <Link to='/video'>
                <div style={{backgroundImage:`url(${cardImage})`}} className={`videocard-img ${classOfImg}`} >
                    <div className="videocard-img-time">{time}</div>
                </div>
            </Link>
            <div className={`videocard__top${nameHidden ? '-hidden' : ''}`}>
                {videoName}
            </div>
            <div className={`videocard__bottom${views ? '-hidden' : ''}`}>
                <div className='videocard__bottom-left'>
                    <div className="videocard__bottom-views">
                        {Math.floor(Math.random() * 300)}k views
                    </div>
                    <pre> - </pre>
                    <div className="videocard__bottom-times">
                        {Math.floor(Math.random() * 13)} month ago
                    </div>
                </div>
                <div className="videocard__bottom-author">
                    {author}          
                </div>
            </div>
        </div>
    )
}
