import React, { useEffect, useState } from 'react'
import '../styles/Channel.scss'
import banner from '../assets/images/banner.png'
import user from '../assets/images/Oval.png'
import { NavLink, Link } from 'react-router-dom'
import VideoCards from './VideoCards'
import user1 from '../assets/images/users/Oval (1).png'
import user2 from '../assets/images/users/Oval (2).png'
import user3 from '../assets/images/users/Oval (3).png'
export default function ChannelPage() {
    const [array, setArray] = useState([]);
    useEffect(()=>{
        fetch('https://jsonplaceholder.typicode.com/photos')
        .then(res=>res.json())
        .then(data=>setArray(data.filter((o,i)=>i < 11)))
    },[])
    const subsNum = Math.floor(Math.random() * 1000) || 1
    return (
        <div className='channel'>
            <img src={banner} alt="" className='channel__banner'/>
            <div className="channel__user">
                <div className='df'>
                    <img src={user} alt="" className="channel__user-img" />
                    <div className="channel__user-data">
                        <div className="channel__user-data-name">Margaret Phelps</div>
                        <div className='channel__user-data-subs'>{subsNum}k subscribed</div>
                    </div>
                </div>
                <div className='channel__user-subs'>Subscribe 2.3m</div>
            </div>
            <ul className="channel__links">
                <li className='channel__links-item'><NavLink to='/channel/' exact className='channel__links-item-link' activeClassName='channel__links-item-link-active'>Home</NavLink></li>
                <li className='channel__links-item'><NavLink to='/channel/videos' className='channel__links-item-link' activeClassName='channel__links-item-link-active'>Videos</NavLink></li>
                <li className='channel__links-item'><NavLink to='/channel/playlist' className='channel__links-item-link' activeClassName='channel__links-item-link-active'>Playlists</NavLink></li>
                <li className='channel__links-item'><NavLink to='/channel/channels' className='channel__links-item-link' activeClassName='channel__links-item-link-active'>Channels</NavLink></li>
                <li className='channel__links-item'><NavLink to='/channel/discussion' className='channel__links-item-link' activeClassName='channel__links-item-link-active'>Discussion</NavLink></li>
                <li className='channel__links-item'><NavLink to='/channel/about' className='channel__links-item-link' activeClassName='channel__links-item-link-active'>About</NavLink></li>
            </ul>
            <div className="channel__main-out">
                <div className="channel__main">
                    <VideoCards views={true} nameHidden={true} cardImage={banner} classOfImg='channel__main-video' />
                    <div>
                    <h3 className="channel__main-name">Choosing The Best Audio Player Software For Your Computer</h3>
                    <p className="channel__main-desc">Your cheap internet-based banner advertising will become one of the sought for ads there are. Today, the world of Internet advertising is rapidly evolving beyond banner ads and intrusive pop-ups. Bayles A common medium for advertising on the Internet is the use of banner ads. </p>
                    <p className='channel__main-views'>11k views    6 months ago</p>
                    </div>
                </div>
                <div className="channel__main-out-right">
                    <div className="channel__main-out-right-name">Recommended channel</div>
                    <div className="channel__main-out-right-links">
                        <Link to='/channel' className='channel__main-out-right-links-item'>
                            <img src={user1} alt="" className='channel__main-out-right-links-item-img'/>
                            <p className="channel__main-out-right-links-item-name">Flora Benson</p>
                        </Link>
                        <Link to='/channel' className='channel__main-out-right-links-item'>
                            <img src={user2} alt="" className='channel__main-out-right-links-item-img'/>
                            <p className="channel__main-out-right-links-item-name">Violet Cobb</p>
                        </Link>
                        <Link to='/channel' className='channel__main-out-right-links-item'>
                            <img src={user3} alt="" className='channel__main-out-right-links-item-img'/>
                            <p className="channel__main-out-right-links-item-name">Phillip Mullins</p>
                        </Link>
                    </div>
                </div>
            </div>
            <div className="channel__bottom">
                <div className="channel__bottom-name">Margaret Phelps videos</div>
                <ul className="channel__bottom-videos">
                    {
                        array.length === 0 ? "You haven't internet connection :(" : array.map(obj => <VideoCards key={obj.id} cardImage={obj.url} classOfImg='channel__bottom-videos-item'/>)
                    }
                </ul>
            </div>
        </div>
    )
}
